import { Form, Input, Modal, Select } from 'antd';
import React from 'react';
import { addDataFormType } from '../../type';

// props
interface CollectionCreateFormProps {
	open: boolean;
	onCreate: (values: addDataFormType) => void;
	onCancel: () => void;
}

// 
const AddDataForm: React.FC<CollectionCreateFormProps> = ({
	open,
	onCreate,
	onCancel,
}) => {
	const [form] = Form.useForm();

	// Modal
	const ModalOnOk = () => {
		form
			.validateFields()
			.then((values: addDataFormType) => {
				form.resetFields();
				onCreate(values);
			})
			.catch((info) => {
				console.log('Validate Failed:', info);
			});
	};

	return (
		<Modal
			getContainer={false}
			title=""
			open={open}
			centered
			okText=""
			cancelText=""
			onCancel={onCancel}
			onOk={ModalOnOk}
			width={700}>
			<Form
				form={form}
				className="addDataForm"
				labelCol={{ span: 4 }}
				wrapperCol={{ span: 14 }}
				layout="horizontal"
				initialValues={{ modifier: 'public' }}
				size="middle">
				<Form.Item
					label=""
					name="name"
					rules={[{ required: true, message: 'Please input name!' }]}>
					<Input />
				</Form.Item>
				<Form.Item
					label=""
					name="project"
					rules={[{ required: true, message: 'Please input project!' }]}>
					<Input />
				</Form.Item>
				<Form.Item
					label=""
					name="grade"
					rules={[{ required: true, message: 'Please select grade!' }]}>
					<Select>
						<Select.Option value="2018">2018</Select.Option>
						<Select.Option value="2019">2019</Select.Option>
						<Select.Option value="2020">2020</Select.Option>
						<Select.Option value="2021">2021</Select.Option>
						<Select.Option value="2022">2022</Select.Option>
					</Select>
				</Form.Item>
				<Form.Item
					label=""
					name="sex"
					rules={[{ required: true, message: 'Please select sex!' }]}>
					<Select>
						<Select.Option value={0}></Select.Option>
						<Select.Option value={1}></Select.Option>
					</Select>
				</Form.Item>
				<Form.Item
					label=""
					name="phone"
					rules={[{ required: true, message: 'Please input phone!' }]}>
					<Input />
				</Form.Item>
				<Form.Item
					label=""
					name="email"
					rules={[{ required: true, message: 'Please input email!' }]}>
					<Input />
				</Form.Item>
				<Form.Item
					label=""
					name="photo"
					rules={[{ required: true, message: 'Please input photo address!' }]}>
					<Input placeholder="/stuphoto.png" />
				</Form.Item>
			</Form>
		</Modal>
	);
};

export default AddDataForm;
