import { Form, Input, Modal, Select } from 'antd';
import React from 'react';
import { DataType } from '../../type';

// props
interface CollectionCreateFormProps {
	open: boolean;
	onCreate: (values: DataType) => void;
	onCancel: () => void;
	editStuData: DataType;
}

const EditDataForm: React.FC<CollectionCreateFormProps> = ({
	open,
	onCreate,
	onCancel,
	editStuData,
}) => {
	const [form] = Form.useForm();

	// setFieldsValue
	form.setFieldsValue({
		id: editStuData.id,
		name: editStuData.name,
		project: editStuData.project,
		grade: editStuData.grade,
		sex: Number(editStuData.sex),
		phone: editStuData.phone,
		email: editStuData.email,
		photo: editStuData.photo,
	});

	// Modal
	const ModalOnOk = () => {
		form
			.validateFields()
			.then((values: DataType) => {
				form.resetFields();
				onCreate(values);
			})
			.catch((info) => {
				console.log('Validate Failed:', info);
			});
	};

	return (
		<Modal
			getContainer={false}
			title=""
			open={open}
			centered
			okText=""
			cancelText=""
			onCancel={onCancel}
			onOk={ModalOnOk}
			width={700}>
			<Form
				form={form}
				className="EditDataForm"
				labelCol={{ span: 4 }}
				wrapperCol={{ span: 14 }}
				layout="horizontal"
				initialValues={editStuData}
				size="middle">
				{/* id */}
				<Form.Item name="id" className="idFormItem" hidden={true}></Form.Item>
				<Form.Item
					label=""
					name="name"
					rules={[{ required: true, message: 'Please input name!' }]}>
					<Input />
				</Form.Item>
				<Form.Item
					label=""
					name="project"
					rules={[{ required: true, message: 'Please input project!' }]}>
					<Input />
				</Form.Item>
				<Form.Item
					label=""
					name="grade"
					rules={[{ required: true, message: 'Please select grade!' }]}>
					<Select>
						<Select.Option value="2018">2018</Select.Option>
						<Select.Option value="2019">2019</Select.Option>
						<Select.Option value="2020">2020</Select.Option>
						<Select.Option value="2021">2021</Select.Option>
						<Select.Option value="2022">2022</Select.Option>
					</Select>
				</Form.Item>
				<Form.Item
					label=""
					name="sex"
					rules={[{ required: true, message: 'Please select sex!' }]}>
					<Select>
						<Select.Option value={0}></Select.Option>
						<Select.Option value={1}></Select.Option>
					</Select>
				</Form.Item>
				<Form.Item
					label=""
					name="phone"
					rules={[{ required: true, message: 'Please input phone!' }]}>
					<Input />
				</Form.Item>
				<Form.Item
					label=""
					name="email"
					rules={[{ required: true, message: 'Please input email!' }]}>
					<Input />
				</Form.Item>
				<Form.Item
					label=""
					name="photo"
					rules={[{ required: true, message: 'Please input photo address!' }]}>
					<Input placeholder="/stuphoto.png" />
				</Form.Item>
			</Form>
		</Modal>
	);
};

export default EditDataForm;
